<template>
  <div>
    <div class="form-container">
      <ProgressCircle :current-step="4" :total-steps="7"></ProgressCircle>
      <h2 class="form-heading">{{ $t('form.postDetails') }}</h2>
      <div class="form-input">
        <label>
          {{ $t('form.PropertyNameTitle') }}
        </label>
        <b-form-input
          :placeholder="$t('form.PropertyNameTitle1')"
          v-model="property.form4.propertyName"
          :state="nameState"></b-form-input>
        <b-form-invalid-feedback>
          Please provide a valid description
        </b-form-invalid-feedback>
      </div>

      <div class="form-input">
        <label>
          {{ $t('form.propertyDescription') }}
        </label>
        <b-form-textarea
          rows="5"
          :placeholder="$t('form.writeSuitableDescription')"
          v-model="property.form4.propertyDescription"
          :state="descriptionState"></b-form-textarea>
        <b-form-invalid-feedback>
          Please provide a valid description
        </b-form-invalid-feedback>
      </div>

      <div class="form-input">
        <div class="mb-3">
          <label>
            {{ $t('form.pricing') }}
          </label>
          <label class="d-block mt-3">
            {{ $t('form.pricePerNight') }}
          </label>
          <b-form-input
            type="number"
            :placeholder="`${$t('form.moneyPlaceholder')} ${$t(
              'form.shortTerm'
            )}`"
            v-model="property.form4.daily_price"
            :state="dailyState"></b-form-input>
          <b-form-invalid-feedback>
            Daily price must be a valid amount
          </b-form-invalid-feedback>
        </div>
        <div>
          <label class="d-block mt-1">
            {{ $t('form.pricePerMonth') }}
          </label>
          <b-form-input
            type="number"
            :placeholder="`${$t('form.moneyPlaceholder')} ${$t(
              'form.longTerm'
            )}`"
            v-model="property.form4.monthly_price"
            :state="monthlyState"></b-form-input>
          <b-form-invalid-feedback>
            Monthly price must be a valid amount
          </b-form-invalid-feedback>
        </div>
      </div>
    </div>

    <div class="page-actions">
      <b-button @click="goBack">{{ $t('common.back') }}</b-button>
      <b-button variant="primary" @click="goNext" :disabled="disabled">
        {{ $t('common.next') }}
      </b-button>
    </div>
  </div>
</template>

<script>
import ProgressCircle from '@/components/postingRequestForm/ProgressCircle';
export default {
  name: 'AddPropertyPage4',
  props: {
    propertyData: {
      type: Object,
      required: true,
    },
  },
  components:{
    ProgressCircle,
  },
  transition: {
    name: 'slide-fade',
    mode: 'out-in',
  },
  data() {
    return {
      currentProperty: null,
      property: {
        form1: null,
        form2: null,
        form3: null,
        form4: {
          propertyName: '',
          propertyDescription: '',
          daily_price: '',
          monthly_price: '',
        },
        form5: null,
        form6: null,
      },
    };
  },
  head() {
    return {
      title: `${this.dir === "ltr" ? "Add Property | Step 4" : "إضافة عقار | الخطوة 4"} ${this.appEnv !== 'production' ? '| Staging' : ''}`,
      meta: [
        // hid is used as unique identifier. Do not use `vmid` for it as it will not work
        {
          hid: 'description',
          name: 'description',
          content: this.dir === "ltr" ? 'Add Property | Property Information' : 'إضافة عقار | معلومات عن العقار'
        }
      ]
    }
  },
  computed: {
    nameState() {
      const { propertyName } = this.property.form4;
      if (!propertyName) return null;
      return propertyName.trim().length > 0;
    },
    descriptionState() {
      const { propertyDescription } = this.property.form4;
      if (!propertyDescription) return null;
      return propertyDescription.trim().length > 0;
    },
    dailyState() {
      const { daily_price } = this.property.form4;
      if (!daily_price) return null;
      return daily_price >= 0;
    },
    monthlyState() {
      const { monthly_price } = this.property.form4;
      if (!monthly_price) return null;
      return monthly_price >= 0;
    },
    disabled() {
      const { daily_price, monthly_price } = this.property.form4;
      if (!this.nameState || !this.descriptionState) return true;
      if (!this.dailyState && !this.monthlyState) return true;
      if (daily_price < 0 || monthly_price < 0) return true;
      return false;
    },
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr';
    },
    appEnv(){
      return this.$config.APP_ENV
    },
  },
  methods: {
    goNext() {
      localStorage.setItem(
        'property',
        JSON.stringify({
          ...this.currentProperty,
          form4: this.property.form4,
        })
      );
      this.$router.push(this.localePath('/properties/add/5'));
    },
    goBack() {
      this.$router.push(this.localePath('/properties/add/3'));
    },
  },
  mounted() {
    window.scrollTo({
      top: 0,
      left: 0,
      behavior: 'smooth',
    });
    this.currentProperty = localStorage.getItem('property') && JSON.parse(localStorage.getItem('property'));

    if (!this.currentProperty)
      this.$router.push(this.localePath('/properties/add/1'));
    if (this.currentProperty.form4) this.property = this.currentProperty;
  },
};
</script>

<style></style>
